<template>
  <div class="table-data">
    <div class="order">
      <div class="head">
        <h3>List of Users</h3>
        <input type="datetime-local" v-model="dueDate" />
        <button class="find" @click="SetDate">Set Closing</button>
        <button class="find" @click="changeAllUserStatus">
          <i class="fa-solid fa-power-off"></i>
          Change All User Status
        </button>
        <div class="d-flex gap-2">
          <button class="find" @click="findData">
            <i class="bx bx-search"></i>Find
          </button>
          <input
            v-model="searchText"
            type="text"
            class="year"
            id="searchText"
          />
        </div>
      </div>
      <table>
        <thead>
          <tr>
            <th>Username</th>
            <th>Office</th>
            <th>Phone No.</th>
            <th>Email</th>
            <th>Status</th>
            <th>Action</th>
            <th>Update</th>
            <th>Contact User</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="UsersInfo in UsersInfo" :key="UsersInfo.user_id">
            <td>{{ UsersInfo.username }}</td>
            <td>{{ UsersInfo.office }}</td>
            <td>{{ UsersInfo.phone_no }}</td>
            <td>{{ UsersInfo.email }}</td>
            <td>{{ UsersInfo.status }}</td>
            <td class="td-btn">
              <button
                class="users-btn"
                @click="toggleUserStatus(UsersInfo.user_id)"
              >
                <i
                  class="fa-solid fa-power-off fa-lg"
                  :class="{
                    'green-btn': UsersInfo.status === 'Enable',
                    'red-btn': UsersInfo.status === 'Disable',
                  }"
                ></i>
              </button>
            </td>
            <td class="td-btn">
              <button class="pen-btn" @click="openForm(UsersInfo)">
                <i class="fa-solid fa-pen fa-lg"></i>
              </button>
            </td>
            <td class="td-btn">
              <button class="users-btn" @click="openForm2(UsersInfo)">
                <i class="fa-solid fa-phone fa-lg"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
  <div class="modal-background" :class="{ 'dim-overlay': formVisible }">
    <form
      class="form"
      id="modal-form3"
      :style="{ display: formVisible ? 'block' : 'none' }"
    >
      <div class="d-flex flex-column my-2">
        <label for="">Username</label>
        <input
          v-model="selectedUser.username"
          type="text"
          placeholder="Username"
          class="input"
        />
      </div>
      <div class="d-flex flex-column my-2">
        <label for="">Office:</label>
        <input
          v-model="selectedUser.office"
          type="text"
          placeholder="Office"
          class="input"
        />
      </div>
      <div class="d-flex flex-column my-2">
        <label for="">Email:</label>
        <input
          v-model="selectedUser.email"
          type="text"
          placeholder="Email"
          class="input"
        />
      </div>
      <div class="d-flex flex-column my-2">
        <label for="">Phone Number:</label>
        <input
          v-model="selectedUser.phone_no"
          type="text"
          placeholder="Phone No."
          class="input"
        />
      </div>

      <div class="modal-buttons">
        <button @click.prevent="saveUser">Save</button>
        <button @click.prevent="closeForm">Close</button>
      </div>
    </form>
  </div>

  <div class="modal-background" :class="{ 'dim-overlay': formVisible2 }">
    <form
      class="form"
      id="modal-form3"
      :style="{ display: formVisible2 ? 'block' : 'none' }"
      @submit.prevent="sendSms"
    >
      <div class="d-flex flex-column gap-2">
        <label for=""> To </label>
        <input
          v-model="selectedUser.phone_no"
          type="text"
          placeholder="Username"
          class="input"
          readonly
          required
        />
        <label for=""> From </label>
        <input
          type="text"
          placeholder="Username"
          class="input"
          value="PRO MIMAROPA ADMINISTRATOR"
          readonly
          required
        />
        <textarea
          v-model="messageContent"
          cols="30"
          rows="10"
          placeholder="Enter message here"
          required
        ></textarea>
      </div>

      <div class="modal-buttons">
        <button type="submit" :disabled="sendingInProgress">Send</button>
        <button @click.prevent="closeForm2">Close</button>
      </div>
    </form>
  </div>

  <div class="modalBg" v-if="formVisible3">
    <div class="alertBox">
      <img class="checkImg" src="./img/check2.gif" alt="" />
      <h1 class="alertContent">Successfully Send</h1>
      <button class="btn btn-primary" @click="closeForm3">Okay</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      UsersInfo: [],
      searchText: "",
      formVisible: false,
      formVisible2: false,
      formVisible3: false,
      messageContent: "",
      selectedUser: {
        id: null,
        username: "",
        office: "",
        phone_no: "",
        email: "",
        password: "",
      },
      sendingInProgress: false,
      dueDate: "",
      selectedDue: "",
      lastInsertedDate: null,
      currentTime: null,
    };
  },

  created() {
    this.getUsersInfo();
    this.getDueDate();
    setInterval(() => {
      this.getDueDate();
      this.checkTime(); // Call checkTime periodically
    }, 600);
  },

  methods: {
    async SetDate() {
      try {
        const response = await axios.post("/insertDue", {
          dueDate: this.dueDate,
        });

        if (response.status === 200) {
          console.log("Successfully set date");
        } else {
          console.log("Unsuccessfully set date");
        }
      } catch (e) {
        console.log(e);
      }
    },

    async getDueDate() {
      try {
        const response = await axios.get("/selectDue");
        this.lastInsertedDate = response.data.date;
        //console.log(this.lastInsertedDate);
      } catch (e) {
        console.log(e);
      }
    },
    checkTime() {
      // Fetch the current time
      const currentDate = new Date();

      // Parse the last inserted date from the database and convert it to a Date object
      const lastInsertedDate = new Date(this.lastInsertedDate);

      // Format the current date to match the database date format
      const formattedCurrentDate = `${currentDate.getFullYear()}-${String(
        currentDate.getMonth() + 1
      ).padStart(2, "0")}-${String(currentDate.getDate()).padStart(
        2,
        "0"
      )} ${String(currentDate.getHours()).padStart(2, "0")}:${String(
        currentDate.getMinutes()
      ).padStart(2, "0")}:${String(currentDate.getSeconds()).padStart(2, "0")}`;

      //console.log(formattedCurrentDate + " " + this.lastInsertedDate);

      // Check if the formatted current date matches the last inserted date
      if (formattedCurrentDate === this.lastInsertedDate) {
        // If they match, execute your other method
        this.changeAllUserStatus();
        this.dueDate = "";
      }
    },

    async getUsersInfo() {
      try {
        const UsersInfo = await axios.get("getUsersInfo");
        this.UsersInfo = UsersInfo.data;
        //console.log(this.UsersInfo);
      } catch (e) {
        console.log(e);
      }
    },

    openForm(UsersInfo) {
      this.selectedUser = { ...UsersInfo };
      this.formVisible = true;
    },

    openForm2(UsersInfo) {
      this.selectedUser = { ...UsersInfo };
      this.formVisible2 = true;
    },

    async saveUser() {
      try {
        const response = await axios.post("/api/saveUser", this.selectedUser);

        if (response.status === 200) {
          const responseData = response.data;

          if (responseData && responseData.success) {
            console.log(responseData.message);
            this.formVisible = false;
            this.getUsersInfo();
          } else {
            console.error("Save failed:", responseData.message);
          }
        } else {
          console.error(`Unexpected response status: ${response.status}`);
        }
      } catch (error) {
        console.error("Error saving admin:", error);
      }
    },

    closeForm() {
      this.formVisible = false;
    },

    closeForm2() {
      this.formVisible2 = false;
    },

    closeForm3() {
      this.formVisible3 = false;
    },

    async changeAllUserStatus() {
      try {
        const response = await axios.post("/changeAllUserStatus");
        if (response.status === 200) {
          const responseData = response.data;

          if (responseData && responseData.success) {
            // console.log(responseData.message);
            this.getUsersInfo(); // Refresh the user list after the status change
          } else {
            console.error("Status change failed:", responseData.message);
          }
        } else {
          console.error(`Unexpected response status: ${response.status}`);
        }
      } catch (error) {
        console.error("Error changing all user status:", error);
      }
    },

    async toggleUserStatus(userInfo) {
      //console.log("userInfo:", userInfo);

      try {
        const response = await axios.post("/toggleUserStatus/", {
          userId: userInfo,
        });
        //console.log("Server response:", response);
        if (response.status === 200) {
          const responseData = response.data;

          if (responseData && responseData.success) {
            //console.log(responseData.message);
            this.getUsersInfo(); // Refresh the user list after the status change
          } else {
            console.error("Status change failed:", responseData.message);
          }
        } else {
          console.error(`Unexpected response status: ${response.status}`);
        }
      } catch (error) {
        console.error("Error changing user status:", error);
      }
    },

    findData() {
      const searchText = document.querySelector("#searchText").value;

      if (searchText === "") {
        this.getUsersInfo();
        return;
      }
      this.UsersInfo = this.UsersInfo.filter((item) => {
        return item.username === searchText;
      });
    },

    async sendSms() {
      try {
        const { phone_no } = this.selectedUser;
        const messageContent = this.messageContent; // Access messageContent directly

        this.sendingInProgress = true;
        const response = await axios.post("/sendSMSToUser", {
          recipient: phone_no,
          message: messageContent,
        });

        if (response.status === 200 && response.data.success) {
          console.log("SMS sent successfully.");
          this.formVisible2 = false; // Hide the SMS form after sending
          this.messageContent = "";
          this.formVisible3 = true;
          setTimeout(() => {
            this.formVisible3 = false;
          }, 5000);
        } else {
          console.error("Failed to send SMS.");
        }
      } catch (error) {
        console.error("Error sending SMS:", error);
      } finally {
        // Set sendingInProgress to false once the process completes
        this.sendingInProgress = false;
      }
    },
  },
};
</script>

<style>
.dim-overlay {
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(
    0,
    0,
    0,
    0.5
  ); /* Adjust the last value for the desired transparency */
  z-index: 1;
  /* Make sure the overlay is above other elements */
}
#modal-form3 {
  position: absolute;
  width: 50%;
  top: 25%;
  left: 37%;
  display: none;
  z-index: 2;
}

.modal-buttons {
  display: flex;
  justify-content: end;
  width: 100%;
  gap: 1rem;
  padding-top: 1rem;
}
.td-btn {
  text-align: center;
}
.fa-solid {
  font-weight: 600;
}
.users-btn {
  color: rgb(47, 212, 47);
}
.green-btn {
  color: rgb(47, 212, 47);
}
.pen-btn {
  color: rgb(233, 70, 70);
}
.red-btn {
  color: rgb(233, 70, 70);
}
</style>
